 @import '../../styles/mixin.scss';

.copy-text {
  display: inline-flex;
  align-items: center;
  max-width: 100%;
  .copy-text-text {
    @include text-ellipsis(100%);
    flex: 1;
    padding-right: 4px;
  }
  .copy-text-action:hover {
    img {
      filter: drop-shadow(0px 0px 4px var(--color-info));
    }
  }
  .copy-text-notification {
    position: fixed;
    font-size: 12px;
    transform: translateY(-50%);
    span {
      margin: 0px 12px;
      white-space: nowrap;
      background-color: var(--color-info); 
      border-color: var(--color-info);
      color: var(--color-white);
    }
  }
}